@import url(https://fonts.googleapis.com/css2?family=Redressed&display=swap);
:root {
  --clr-drk-blue: rgb(61, 90, 128);
  --clr-med-blue: rgb(152, 193, 217);
  --clr-lgt-blue: rgb(224, 251, 252);
  --clr-orange: rgb(238, 108, 77);
  --clr-drk-grey: rgb(41, 50, 65);
  --clr-lgt-grey: rgba(41, 50, 65, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style-type: none;
}

body {
  background-color: lightgrey;
}

h1 {
  font-family: "Audiowide", Helvetica, sans-serif;
}

p {
  font-family: "Redressed", Helvetica, sans-serif;
  font-size: 1.25rem;
  line-height: 1.3;
}

header {
  background-color: rgb(41, 50, 65);
  background-color: var(--clr-drk-grey);
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  font-family: "Audiowide", Helvetica, sans-serif;
}
/* ! ---------------------------------------- */
/* !           MOBILE / SMART-PHONE           */
/* ! ---------------------------------------- */

.content {
  border-top: 2px solid black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;

  background-color: var(--clr-drk-grey);
}

.content>* {
  text-align: center;
}

.content h1 {
  margin-top: 20px;
  color: lightgrey;
  text-shadow: 1px 1px 1px black,
    2px 2px 1px orange,
    4px 4px 2px black;
}

.content p {
  margin-top: 10px;
  margin-bottom: 20px;
  padding: 0 40px;
  color: lightgrey;
}

/* ! ---------------------------------------- */
/* !                 TABLET                   */
/* ! ---------------------------------------- */
@media only screen and (min-width: 768px) {
  .content {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

  .content h1 {
    margin-left: 10px;
    margin-top: 10px;
    padding-right: 15px;
    -webkit-flex-basis: 25%;
            flex-basis: 25%;
    -webkit-align-self: flex-start;
            align-self: flex-start;
    justify-self: flex-end;
    text-align: right;
  }

  .content p,
  .work {
    border-left: 2px solid lightgrey;

    padding-left: 15px;
    -webkit-flex-basis: 65%;
            flex-basis: 65%;

    -webkit-align-self: flex-start;

            align-self: flex-start;
    text-align: left;
    font-size: 1.5rem;
  }
}

/* ! ---------------------------------------- */
/* !                DESKTOP                   */
/* ! ---------------------------------------- */


@media only screen and (min-width: 1025px) {

  .content h1,
  .content p,
  .content div {
    margin-top: 30px;
  }

  .content p,
  .work {
    -webkit-flex-basis: 60%;
            flex-basis: 60%;
    margin-right: 100px;
    margin-bottom: 30px;
  }
}
/* ! ---------------------------------------- */
/* !           MOBILE / SMART-PHONE           */
/* ! ---------------------------------------- */

.hero-jumbotron {
  height: 50vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.hero-jumbotron::before {
  background-image: url(/portfolio-react/static/media/cover-bg.c336b5ce.jpg);

  background-size: cover;

  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;

  opacity: 0.1;
}

.hero-jumbotron h1 {
  font-family: "Audiowide", cursive;
  font-size: 2rem;
}

.hero-jumbotron p {
  text-align: center;
  width: min(600px, 90%);
  font-size: 1.5rem;
  font-family: "Redressed", scursive;
}

/* ! ---------------------------------------- */
/* !                 TABLET                   */
/* ! ---------------------------------------- */
@media only screen and (min-width: 768px) {
  .hero-jumbotron h1 {
    font-size: 2.5rem;
  }
  .hero-jumbotron p {
    font-size: 2rem;
  }
}

